// Google Fonts
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
$font: 'Roboto', serif;

// Color
$primary: #00378b;
$rgba: rgba(0, 55, 139, 0.75);
$secondary: #ce2f87;
///////////////
$spans: 50;

@font-face {
  font-family: 'Lineto circular pro';
  src: url('./fonts/lineto-circular-pro-black.woff2') format('woff2'),
    url('./fonts/lineto-circular-pro-black.eot') format('embedded-opentype'),
    url('./fonts/lineto-circular-pro-black.woff') format('woff');
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  letter-spacing: 0;
  line-height: 1.875;
  font-variant-ligatures: common-ligatures;
  font-feature-settings: 'ss02';
  background-color: #fff;
  background-image: linear-gradient(
    260deg,
    rgba(86, 167, 216, 0.08),
    rgba(86, 167, 216, 0.15)
  );
  color: $rgba;
  p {
    font-size: 2.5rem;
  }
  .background {
    background: url('../img/pipelines.svg') no-repeat fixed right;
    opacity: 0.7;
  }
  a {
    color: $primary !important;
  }
  a:hover {
    color: $secondary !important;
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-family: 'Lineto circular pro', sans-serif !important;
  font-weight: 700 !important;
  color: $primary;
}

.bg-fuchsia {
  background-color: $secondary;
  color: white;
  h1,
  .h1,
  h2,
  .h2,
  h3,
  .h3 {
    color: white;
  }
}

#page-background-right {
  float: right;
  width: 50%;
}

.flexblock.features {
  h2,
  li span {
    margin-right: 0 !important;
    font-size: 2.5rem !important;
  }
  li h2 {
    text-transform: none !important;
  }
}

.flexblock.blink li > a {
  align-content: end !important;
  justify-content: space-evenly !important;
}
